import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('pages/home/HomePage')
const Live = () => import('pages/live/LiveVideoPage')
const News = () => import('pages/news/NewsPage')
const Videos = () => import('pages/videos/VideoListPage')

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    meta:{
      title: '首页'
    }
  },
  {
    path: '/live',
    name: 'Live',
    component: Live,
    meta:{
      title: '直播'
    }
  },
  {
    path: '/news',
    name: 'News',
    component: News,
    meta:{
      title: '播报'
    }
  },
  {
    path: '/videos',
    name: 'Videos',
    component: Videos,
    meta:{
      title: '视频'
    }
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.afterEach((to, from, next) => {
  let preTitle="iPanda熊猫频道";

  if (to.meta.title) {
    document.title = preTitle+"_"+to.meta.title
  } else {
    document.title = preTitle;
  }
})

export default router
